import { h } from "preact";

import cardClasses from "./CardList.module.css";

type CardProps = {
  Wrapper?: h.JSX.ElementType;
} & h.JSX.HTMLAttributes;

export function CardList({
  children,
  className,
  ...props
}: Readonly<h.JSX.HTMLAttributes<HTMLDivElement>>) {
  return (
    <div
      {...{
        className: `${cardClasses["items-list-root"]} ${className}`,
        ...props,
      }}
    >
      {children}
    </div>
  );
}

export function Card({
  Wrapper,
  className,
  children,
  ...props
}: Readonly<CardProps>) {
  const propsToWrapper = {
    className: `${cardClasses["list-item"]} ${className}`,
    ...props,
  };
  if (!Wrapper) {
    return (
      <div {...(propsToWrapper as h.JSX.HTMLAttributes<HTMLDivElement>)}>
        {children}
      </div>
    );
  }

  return <Wrapper {...propsToWrapper}>{children}</Wrapper>;
}
